function ThemNhanVien() {
    // (manhanvien,ho,ten,ngaysinh,sodienthoai,email,ngaylam,chucvu)
    var manv = document.getElementById('msnv').value;
    var ho = document.getElementById('ho').value;
    var ten = document.getElementById('ten').value;
    var luongcoban = document.getElementById('luongcoban').value;
    luongcoban = parseInt(luongcoban);
    var phucap = document.getElementById('phucap').value;
    phucap = parseInt(phucap);
    var hoahong = document.getElementById('hoahong').value;
    hoahong = parseInt(hoahong);
    //     let ngaylam =  (<HTMLInputElement> document.getElementById('')).value;
    var loainv = kiemTraLoaiNhanVien();
    if (loainv === null) {
        alert("nhập vào dùm cái nè");
    }
    else {
        var nhanVienMoi = void 0;
        if (loainv == "nhanvien") {
            // (manhanvien:string,ho:string,ten:string,luongcoban:number,chucvu:string)
            nhanVienMoi = new NhanVien_1.NhanVien(manv, ho, ten, luongcoban, loainv);
        }
        else {
            if (loainv == "quanly") {
                nhanVienMoi = new QuanLy_1.QuanLy(manv, ho, ten, luongcoban, loainv, hoahong);
            }
            else {
                if (loainv == "giamdoc") {
                    nhanVienMoi = new GiamDoc_1.GiamDoc(manv, ho, ten, luongcoban, loainv, phucap);
                }
            }
        }
    }
}
function kiemTraLoaiNhanVien() {
    var mangLoaiNhanVien = document.getElementsByClassName('loainv');
    for (var i in mangLoaiNhanVien) {
        if (mangLoaiNhanVien[i].checked) {
            return mangLoaiNhanVien[i].value;
        }
    }
    return null;
}
